"use client";

import { useRouter } from "next/navigation";
import { useState } from 'react';
import { useAudioRecorder } from "./audio_recorder";
import { setSpeechText } from '@/store/slices/speechSlice'
import { Spin } from 'antd';
import { useDispatch } from 'react-redux'

const ContextSection = () => {
  const router = useRouter();
  const { isRecording, startRecording, stopRecording } = useAudioRecorder();
  const [isUploading, setIsUploading] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);

  const dispatch = useDispatch()

  const handleButtonClick = async () => {
    if (isRecording) {
      setIsProcessing(true);
      try {
        // ç›´æ¥ä» stopRecording è·å– blob
        const recordedBlob = await stopRecording();
        
        if (!recordedBlob) {
          console.warn('No audio blob available after recording stopped');
          setIsProcessing(false);
          return;
        }
        
        // ä½¿ç”¨ç›´æ¥è·å–çš„ blob è¿›è¡Œä¸Šä¼ ï¼Œä¸ä¾èµ–ç»„ä»¶çŠ¶æ€
        await handleUploadWithBlob(recordedBlob);
      } catch (error) {
        console.error('Recording error:', error);
      } finally {
        setIsProcessing(false);
      }
    } else {
      startRecording();
    }
  };

  // æ–°å¢ä¸€ä¸ªå‡½æ•°ï¼Œç›´æ¥ä½¿ç”¨ä¼ å…¥çš„ blob
  const handleUploadWithBlob = async (blob: Blob) => {
    try {
      setIsUploading(true);
      const formData = new FormData();
      formData.append('audio', blob, 'recording.webm');

      const response = await fetch('https://blabit.xyz/transcribe', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        throw new Error('Upload failed');
      }

      const data = await response.json();
      console.log('Upload successful:', data);
      
      dispatch(setSpeechText(data.text));
      router.push('/context');
    } catch (error) {
      console.error('Error uploading audio:', error);
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <div 
      className="flex flex-col items-center justify-center min-h-screen bg-[rgb(183,198,175)] p-0 relative"
    >
      {/* å…¨å±åŠ è½½åŠ¨ç”» */}
      {isUploading && (
        <div className="fixed inset-0 flex items-center justify-center bg-[rgba(183,198,175,0.8)] z-50">
          <Spin 
            size="large" 
            tip="Processing..." 
            style={{ 
              color: '#8B4513',
            }}
            fullscreen 
          />
        </div>
      )}

      {/* å†…å®¹å®¹å™¨ - æ·»åŠ æœ€å¤§å®½åº¦å¹¶å±…ä¸­ */}
      <div className="w-full max-w-[800px] mx-auto relative h-full min-h-screen">
        {/* å›¾ç‰‡å±‚ - ä½¿ç”¨ç»å¯¹å®šä½ä½œä¸ºèƒŒæ™¯ */}
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 opacity-70 z-0 w-4/5">
          <div className="w-full">
            <img
              src="/images/1.jpg"
              alt="Context Whisperer Logo"
              className="rounded-lg shadow-md object-cover w-full h-auto"
            />
          </div>
        </div>

        {/* é‡æ–°æ’ç‰ˆçš„æ–‡å­—å±‚ */}
        <div className="absolute z-10 flex flex-col h-full w-full px-8">
          {/* The Context */}
          <div className="h-[13%] flex items-end pt-8 pl-8">
            <h1 className="text-2xl sm:text-3xl md:text-4xl font-bold text-gray-600">
              The Context
            </h1>
          </div>
          
          {/* Whisperer */}
          <div className="h-[5%] flex justify-center items-center">
            <h1 className="text-4xl sm:text-5xl md:text-6xl font-bold text-gray-800">
              Whisperer
            </h1>
          </div>
          
          {/* æ·»åŠ å¡«å……å™¨ç©ºé—´ */}
          <div className="h-[25%]"></div>
          
          {/* æ–°çš„ç»“æ„ï¼šæ–‡å­—å’ŒæŒ‰é’®åœ¨åŒä¸€ä¸ªå®¹å™¨ä¸­ */}
          <div className="h-[57%] w-full flex flex-col relative">
            {/* ç»ç’ƒæ•ˆæœèƒŒæ™¯ */}
            <span className="absolute inset-0 bg-white/25 backdrop-blur-xl backdrop-filter backdrop-saturate-150 rounded-lg z-0"></span>
            
            <div className="flex flex-col h-full p-6 pt-8 z-10 relative">
              {/* ä¸»é¢˜æ–‡å­—éƒ¨åˆ† - å æ®å®¹å™¨çš„65% */}
              <div className="flex-1 min-h-0 mb-8 flex items-center">
                <p className="text-base sm:text-xl md:text-2xl text-gray-800 w-full text-justify">
                  Like a gentle breeze carrying the first words of spring, our context generator crafts the perfect opening lines for your conversations. It weaves together phrases and translations, helping you step confidently into any dialogue, as naturally as dawn breaks over the horizon.
                </p>
              </div>
              
              {/* æŒ‰é’®éƒ¨åˆ† - å æ®å®¹å™¨çš„30% */}
              <div className="h-[30%] min-h-[60px] flex items-center justify-center px-4">
                <button
                  onClick={handleButtonClick}
                  disabled={isProcessing}
                  className={`px-6 py-3 text-lg sm:text-xl md:text-2xl rounded-lg font-semibold select-none w-full shadow-lg flex items-center justify-center bg-black hover:bg-gray-800 text-[rgb(183,198,175)] transition-colors ${
                    isProcessing ? 'opacity-50 cursor-not-allowed' : ''
                  }`}
                >
                  {isProcessing ? 'PROCESSING...' : isRecording ? 'STOP RECORDING' : 'SET THE CONTEXT FOR ME'}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContextSection;