import { useState, useRef } from 'react';

export const useAudioRecorder = () => {
  const [isRecording, setIsRecording] = useState(false);
  const [audioBlob, setAudioBlob] = useState<Blob | null>(null);
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const chunksRef = useRef<Blob[]>([]);

  const startRecording = async () => {
    try {
      setIsRecording(true);
      // æ·»åŠ  iOS Safari æ‰€éœ€çš„çº¦æŸ
      const stream = await navigator.mediaDevices.getUserMedia({
        audio: {
          echoCancellation: true,
          noiseSuppression: true,
          autoGainControl: true,
          sampleRate: 44100,
          channelCount: 1
        }
      });

      // æ£€æŸ¥æµè§ˆå™¨æ”¯æŒçš„ MIME ç±»å‹
      const mimeType = [
        'audio/webm',
        'audio/mp4',
        'audio/aac',
        'audio/ogg'
      ].find(type => MediaRecorder.isTypeSupported(type)) || 'audio/webm';

      mediaRecorderRef.current = new MediaRecorder(stream, {
        mimeType,
        audioBitsPerSecond: 128000
      });
      
      // è®¾ç½®è¾ƒå°çš„æ—¶é—´ç‰‡ï¼Œæé«˜å…¼å®¹æ€§
      mediaRecorderRef.current.ondataavailable = (event) => {
        if (event.data.size > 0) {
          chunksRef.current.push(event.data);
        }
      };

      mediaRecorderRef.current.onstop = () => {
        try {
          if (chunksRef.current.length > 0) {
            const blob = new Blob(chunksRef.current, { type: mimeType });
            setAudioBlob(blob);
          } else {
            console.warn('No audio data chunks were collected during recording');
          }
          chunksRef.current = [];
        } catch (error) {
          console.error('Error creating audio blob:', error);
        }
      };

      // æ¯ç§’æ”¶é›†ä¸€æ¬¡æ•°æ®ï¼Œé¿å…å†…å­˜é—®é¢˜
      mediaRecorderRef.current.start(1000);
    } catch (error) {
      console.error('Error accessing microphone:', error);
      setIsRecording(false);
      // æ·»åŠ æ›´è¯¦ç»†çš„é”™è¯¯å¤„ç†
      if (error instanceof DOMException) {
        if (error.name === 'NotAllowedError') {
          alert('è¯·å…è®¸è®¿é—®éº¦å…‹é£æƒé™');
        } else if (error.name === 'NotFoundError') {
          alert('æœªæ‰¾åˆ°å½•éŸ³è®¾å¤‡');
        } else {
          alert('å½•éŸ³åˆå§‹åŒ–å¤±è´¥ï¼Œè¯·æ£€æŸ¥è®¾å¤‡æƒé™');
        }
      }
    }
  };

  const stopRecording = () => {
    return new Promise<Blob | null>((resolve) => {
      if (mediaRecorderRef.current && isRecording) {
        try {
          setIsRecording(false);
          
          mediaRecorderRef.current.onstop = () => {
            try {
              if (chunksRef.current.length > 0) {
                const blob = new Blob(chunksRef.current, { 
                  type: mediaRecorderRef.current?.mimeType || 'audio/webm' 
                });
                setAudioBlob(blob); // æ›´æ–°çŠ¶æ€
                resolve(blob);      // ç›´æ¥è¿”å› blob
              } else {
                console.warn('No audio data chunks were collected during recording');
                resolve(null);
              }
              chunksRef.current = [];
            } catch (error) {
              console.error('Error creating audio blob:', error);
              resolve(null);
            }
          };
          
          mediaRecorderRef.current.stop();
          mediaRecorderRef.current.stream.getTracks().forEach(track => {
            track.stop();
            track.enabled = false;
          });
        } catch (error) {
          console.error('Error stopping recording:', error);
          setIsRecording(false);
          resolve(null);
        }
      } else {
        resolve(null);
      }
    });
  };

  return {
    isRecording,
    audioBlob,
    startRecording,
    stopRecording
  };
};
